import React from 'react';
import { Button, Card, Col, Divider, Input, Row } from "antd";
import { ROUTE_PATH } from "../App";
import { useRecoilState } from "recoil";
import { horseState } from "../store";
import { useNavigate } from "react-router-dom";
import styled from "@emotion/styled";
import FlexCenter from "../components/FlexCenter";

const Experience = () => {
  let [horse, setHorse] = useRecoilState(horseState);
  const navigate = useNavigate();
  localStorage.removeItem('settledTime');



  const onButtonClick = () => {
    navigate(ROUTE_PATH.TICK)
  }

  return (
    <Row gutter={[8, 8]}>
      <Col span={24}>
        <Card
          title=' '
          size='small'
        >
          <Row>
            <Col span={12}> : </Col>
            <Col span={12}>{horse.teer}</Col>
            <Col span={12}> : </Col>
            <Col span={12}>{horse.currentLevel} Lv</Col>
          </Row>
        </Card>
      </Col>
      <Col span={24} style={{ margin: '2rem 0 0 0' }}>
        <P><b>!</b> 2  .</P>
      </Col>
      <Col span={24}>
        <P>     .</P>
      </Col>
      <Col span={24}>
        <Input
          pattern="[0-9]*"
          size='large'
          onChange={
            (e) => {
              localStorage.setItem('currentExperience', e.target.value);
              setHorse((prev) => {
                return ({
                  ...prev,
                  currentExperience: parseInt(e.target.value)
                })
              })
            }
          }
          placeholder='ex) 41000 ->   '
          style={{ marginBottom: '1rem' }}
          type="number"
        />
      </Col>
      <Col span={24}>
        <P>    .</P>
      </Col>
      <Col span={24}>
        <Input
          pattern="[0-9]*"
          size='large'
          onChange={
            (e) => {
              localStorage.setItem('nextExperience', e.target.value);
              setHorse((prev) => {
                return ({
                  ...prev,
                  nextExperience: parseInt(e.target.value)
                })
              })
            }
          }
          onKeyDown={(e) => {
            if(e.key === 'Enter') {
              onButtonClick();
            }
          }}
          placeholder='ex) 42000 ->  '
          style={{ marginBottom: '1rem' }}
          type="number"
        />
      </Col>
      <Col span={24}>
        <Divider />
      </Col>
      <Col span={24}>
        <FlexCenter>
          <CircleButton
            onClick={onButtonClick}
          >
            
          </CircleButton>
        </FlexCenter>
      </Col>
    </Row>
  );
};

const CircleButton = styled(Button)`
  width: 100px;
  height: 100px;
  border-radius: 50%;
`

const P = styled.p`
  font-size: 1.2rem;
  margin: 0;
`

export default Experience;